.product__rating{
    display: flex;
}
.product{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    width: 100%;
    padding: 20px;
    max-height: 400px;
    min-width: 100px;
    background-color: white;
    margin: 10px;
    z-index: 1;

}
.product__price{
    margin-top: 5px;
}
.product>img{
    max-height: 200px;
    width:100%;
    object-fit: contain;
    margin-bottom: 15px;
}
.product__info{
    height:100px;
    margin-bottom: 15px;

}
.product>button{
    background-color: #f0c14b;
    border:1px solid ;
    border-color: #a88734 #9c7e31 #846a29;
    margin-top: 10px;
    margin-top: 10px;
}